@import './colors';

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  overflow-y: scroll;
  width: 100%;

  .modalBackProject {
    position: relative;
    // top: 100%;
    // left: 50%;
    // transform: translate(-50%, -50%);
    z-index: 900;
    background-color: #fff;
    // background-color: #f1f1f1;
    // height: fit-content;
    width: 80%;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    padding: 30px 25px;
    // max-height: calc(100vh - 210px);
    // overflow-y: scroll !important;
    margin-top: 400px;
    margin-bottom: 100px;
    // overflow-y: scroll;
    .topLine {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    p {
      text-align: left;
      color: $greyDark;
    }
  }
}

@media screen and (min-width: 1440px) {
  .modalBackProject {
    max-width: 1000px;
  }
}
